import React, { useState, useLayoutEffect } from 'react';
import clsx from 'clsx';
import Heading from '@elements/heading';
import useMousePointer from '@hooks/useMousePointer';
import styles from './main-section.module.scss';

const MainSection = () => {
  const { mousePosition, setRef } = useMousePointer();
  const { isHovered, setIsHovered } = useState(false);

  console.log({ ...mousePosition });
  const classes = clsx(
    styles.stl__heading,
    mousePosition.isOver && styles['stl__heading--is-hovered']
  );

  useLayoutEffect(() => {
    setIsHovered(mousePosition.isOver);
  }, [mousePosition.isOver]);

  return (
    <div className={styles.stl}>
      <Heading ref={setRef} className={classes} tag="h1">
        Jonatán Isaac
        <br />
        Treviño Galindo
      </Heading>
    </div>
  );
};

export default MainSection;
